import React, { Component} from 'react';
import MenuItem from './MenuItem';
import cookie from "react-cookies";
import strings from "../../Localisation";
import Logo from './Logo'
import ShortForm from './ShortForm'
import nextId from "react-id-generator";
import LanguageItem from "./LanguageItem";
import {sendAmplitudeData} from "../../../utils/Amplitude";

import {PATH_ABOUT, PATH_CONTACT, PATH_MAIN, PATH_STUFF} from "../../../utils/Paths";

const RU_LANGUAGE = "ru";
const EN_LANGUAGE = "en";



export default class Header extends Component {
    constructor(props) {
        super(props);

        this.languageRuShort = nextId();
        this.languageEnShort = nextId();
        this.languageRuFull = nextId();
        this.languageEnFull = nextId();

        let language = cookie.load("language");
        language = language? language : RU_LANGUAGE;

        this.state = {
            language: language,
            width: 2000
        };

        this.changeLanguage(language);
    }

    componentDidMount() {
        this.setState({
            width: document.body.clientWidth
        });


        let buttonEnShort = document.getElementById(this.languageEnShort);
        let buttonRuShort = document.getElementById(this.languageRuShort);
        let buttonEnFull = document.getElementById(this.languageEnFull);
        let buttonRuFull = document.getElementById(this.languageRuFull);

        let link = this;

        buttonEnShort.addEventListener('click', function (event) {
            event.preventDefault();
            link.logLanguage(EN_LANGUAGE);
            link.changeLanguage(EN_LANGUAGE);
        });

        buttonRuShort.addEventListener('click', function (event) {
            event.preventDefault();
            link.logLanguage(RU_LANGUAGE);
            link.changeLanguage(RU_LANGUAGE);
        })

        buttonEnFull.addEventListener('click', function (event) {
            event.preventDefault();
            link.logLanguage(EN_LANGUAGE);
            link.changeLanguage(EN_LANGUAGE);
        });

        buttonRuFull.addEventListener('click', function (event) {
            event.preventDefault();
            link.logLanguage(RU_LANGUAGE);
            link.changeLanguage(RU_LANGUAGE);
        })
    }

    logLanguage(lang) {
        sendAmplitudeData("IntroScreen: changed language on " + lang);
    }

    changeLanguage(lang) {
        strings.setLanguage(lang);

        cookie.save("language", strings.getLanguage());
        this.setState({ language: lang });
        this.props.handle();
    }

    render() {
        return (
            <div>
                <div className={"header_up_space"}/>

                <div className={"header"}>
                    <Logo/>

                    <div className={"header_menu"}>
                        <MenuItem
                            type={"main"}
                            path={PATH_MAIN}
                            text={strings.header__main}/>

                        <MenuItem
                            type={"ordinary"}
                            path={PATH_ABOUT}
                            text={strings.header__about}/>

                        <MenuItem
                            type={"ordinary"}
                            path={PATH_CONTACT}
                            text={strings.header__contact}/>

                        <MenuItem
                            type={"ordinary"}
                            path={PATH_STUFF}
                            text={strings.header__stuff}/>

                        <MenuItem
                            type={"survey"}
                            path={strings.header__survey_link}
                            text={strings.header__survey}/>

                        <div className={"header_languages__shortsize"}>
                            <LanguageItem
                                isLeft={true}
                                id={this.languageEnShort}
                                text={EN_LANGUAGE}
                                isActive={this.state.language === EN_LANGUAGE}/>

                            <LanguageItem
                                isLeft={false}
                                id={this.languageRuShort}
                                text={RU_LANGUAGE}
                                isActive={this.state.language === RU_LANGUAGE}/>

                        </div>
                    </div>

                    <div className={"header_languages__fullsize"}>
                        <LanguageItem
                            isLeft={true}
                            id={this.languageEnFull}
                            text={EN_LANGUAGE}
                            isActive={this.state.language === EN_LANGUAGE}/>

                        <LanguageItem
                            isLeft={false}
                            id={this.languageRuFull}
                            text={RU_LANGUAGE}
                            isActive={this.state.language === RU_LANGUAGE}/>

                    </div>

                    <ShortForm/>
                </div>
            </div>
        )
    }
}